.Layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;

    -webkit-animation-duration: 12s;

            animation-duration: 12s;
}

.Layer.day-to-night {
    -webkit-animation-name: day-to-night;
            animation-name: day-to-night;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

.Layer.lights-on {
    -webkit-animation-name: lights-on;
            animation-name: lights-on;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes day-to-night {
    0% {
        opacity: 0
    }
    40%, 60% {
        opacity: 1;
    }
    100% {
        opacity: 0
    }
}

@keyframes day-to-night {
    0% {
        opacity: 0
    }
    40%, 60% {
        opacity: 1;
    }
    100% {
        opacity: 0
    }
}

@-webkit-keyframes lights-on {
    0%, 32.5% {
        opacity: 0;
    }
    33%, 50% {
        opacity: 1;
    }
    51%, 100% {
        opacity: 0;
    }
}

@keyframes lights-on {
    0%, 32.5% {
        opacity: 0;
    }
    33%, 50% {
        opacity: 1;
    }
    51%, 100% {
        opacity: 0;
    }
}


